import React from 'react'
import domToReact from 'html-react-parser/lib/dom-to-react'

export const listGist = ({ attribs, children, parent }) => {
  if (!attribs) return
  if (attribs.class && attribs.class.includes('ltag_gist-liquid-tag')) {
    if (parent && parent.name === 'li') {
      return (
        <div className="highlight indented">
          {domToReact(children)}
        </div>
      )
    }
  }
}
