import React from 'react'
import Parser from 'html-react-parser'
import styled from '@emotion/styled'

import { linkBlock, listCode, listGist } from './blocks'
import Card from './Card'
import Profile from './Profile'

const BlogPostStyle = styled.div`
  color: var(--theme-font-color);
  h1.title {
    margin: 0;
    font-size: calc(1.85vw + 25px);
    line-height: calc(1.85vw + 35px);
    font-weight: 500;
    a {
      text-decoration: none;
      color: var(--theme-font-color);
    }
  }

  div.body {
    h1 {
      font-size: 1.85em;
      line-height: 1.14em;
      font-weight: 400;
    }
    div.highlight pre {
      padding: 5% 5% 5% 20px;
      margin: 20px -20px;
      overflow-wrap: normal;
      overflow-x: auto;
    }
    div.highlight.indented {
      pre {
        margin-left: -125px;
        padding-left: 125px;
      }
    }
    img {
      max-width: 100%;
    }
    iframe {
      margin: 0 auto;
      display: block;
      width: 100%;
    }
  }

  strong {
    word-break: break-word;
  }

  @media only screen and (min-width: 600px) {
    div.body {
      div.highlight pre {
        padding: 5% 5% 5% 85px;
        margin: 20px -85px;
      }
      iframe {
        width: auto;
      }
    }
  }
`

const BlogFooter = styled.div`
  margin: 20px 0;
`

const BlogPost = ({ post }) => {
  const removeWrappers = html => {
    ;['html', 'body'].forEach(tag => {
      html = html.replace(`<${tag}>`, '')
      html = html.replace(`</${tag}>`, '')
    })
    return html
  }
  const Content = Parser(removeWrappers(post.body_html), {
    replace: node => {
      let content
      ;[linkBlock, listCode, listGist].some(transform => {
        return (content = transform(node))
      })
      return content
    },
  })
  return (
    <>
      <Card>
        <BlogPostStyle>
          <h1 className="title">
            <a href={`/${post.slug}`}>{post.title}</a>
          </h1>
          <div className="body">{Content}</div>
        </BlogPostStyle>
      </Card>
      <BlogFooter>
        <Profile />
      </BlogFooter>
    </>
  )
}

export default BlogPost
