import React, { useRef, useEffect } from 'react'
import styled from '@emotion/styled'
import { useDispatch, useSelector } from 'react-redux'

const ToggleStyle = styled.div`
  &.react-toggle {
    touch-action: pan-x;

    display: inline-block;
    position: relative;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    padding: 0;

    -webkit-touch-callout: none;
    user-select: none;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
  }

  .react-toggle-screenreader-only {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  .react-toggle-track {
    width: 50px;
    height: 24px;
    padding: 0;
    border-radius: 30px;
    background-color: hsl(222, 14%, 7%);
    transition: all 0.2s ease;
  }

  .react-toggle-track-check {
    position: absolute;
    width: 17px;
    height: 17px;
    left: 5px;
    top: 0px;
    bottom: 0px;
    margin-top: auto;
    margin-bottom: auto;
    line-height: 0;
    opacity: 0;
    transition: opacity 0.25s ease;
  }

  &.react-toggle--checked .react-toggle-track-check {
    opacity: 1;
    transition: opacity 0.25s ease;
  }

  .react-toggle-track-x {
    position: absolute;
    width: 17px;
    height: 17px;
    right: 5px;
    top: 0px;
    bottom: 0px;
    margin-top: auto;
    margin-bottom: auto;
    line-height: 0;
    opacity: 1;
    transition: opacity 0.25s ease;
  }

  &.react-toggle--checked .react-toggle-track-x {
    opacity: 0;
  }

  .react-toggle-thumb {
    position: absolute;
    top: 1px;
    left: 1px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #fafafa;
    box-sizing: border-box;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transform: translateX(0);
  }

  &.react-toggle--checked .react-toggle-thumb {
    transform: translateX(26px);
    border-color: #19ab27;
  }

  &.react-toggle:active .react-toggle-thumb {
    box-shadow: 0px 0px 5px 5px #9c9c9c;
  }
`

const Toggle = () => {
  const checkbox = useRef(null)
  const dispatch = useDispatch()
  const isDarkMode = useSelector(({ app }) => app.isDarkMode)

  useEffect(() => {
    checkbox.current.checked = isDarkMode
    window.__onThemeChange = () => {
      dispatch({
        type: 'TOGGLE_DARK_MODE',
        isDarkMode: window.__theme === 'dark',
      })
    }
  }, [])

  function toggle(event) {
    if (event.target !== checkbox) {
      event.preventDefault()
      checkbox.current.focus()
      checkbox.current.click()
      window.__setPreferredTheme(isDarkMode ? 'light' : 'dark')
      dispatch({
        type: 'TOGGLE_DARK_MODE',
        isDarkMode: !isDarkMode,
      })
      return
    }
  }

  return (
    <ToggleStyle
      className={`react-toggle ${isDarkMode ? ' react-toggle--checked' : ''}`}
      onClick={toggle}
    >
      <div className="react-toggle-track">
        <div className="react-toggle-track-check">
          <img
            alt="night"
            src="/assets/moon.png"
            width={16}
            height={16}
            role="presentation"
            style={{ pointerEvents: 'none' }}
          />
        </div>
        <div className="react-toggle-track-x">
          <img
            alt="day"
            src="/assets/sun.png"
            width={16}
            height={16}
            role="presentation"
            style={{ pointerEvents: 'none' }}
          />
        </div>
      </div>
      <div className="react-toggle-thumb" />
      <input
        ref={checkbox}
        className="react-toggle-screenreader-only"
        type="checkbox"
        aria-label="Switch between Dark and Light mode"
      />
    </ToggleStyle>
  )
}

export default Toggle
