import React from 'react'
import domToReact from 'html-react-parser/lib/dom-to-react'

export const listCode = ({ attribs, children, parent }) => {
  if (!attribs) return
  if (attribs.class && attribs.class.includes('highlight')) {
    if (parent && parent.name === 'li') {
      attribs.className = attribs.class
      delete attribs.class 
      return (
        <div className="highlight indented">
          <pre {...attribs}>{domToReact(children)}</pre>
        </div>
      )
    }
  }
}
