import React from 'react'
import styled from '@emotion/styled'
import { graphql, useStaticQuery } from 'gatsby'

const ProfileStyle = styled.div`
  display: flex;
  width: 345px;
  margin: 0 auto;
  margin-bottom: 20px;
  img {
    display: inline-block;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    min-width: 55px;
    max-width: 100%;
    box-shadow: var(--theme-container-box-shadow, 3px 3px 0px #bababa);
    border: var(--theme-container-border, 1px solid #d6d6d6);
  }
  div {
    color: var(--theme-font-color, #0a0a0a);
    padding-left: 20px;

    h4 {
      margin: 0;
      font-size: 18px;
    }
    p {
      margin-top: 5px;
      font-size: 15px;
      line-height: 20px;
    }
  }
  @media only screen and (min-width: 600px) {
    width: auto;
    max-width: 650px;
  }
`

const Profile = () => {
  const {
    site: { siteMetadata },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          name
          shortBio
        }
      }
    }
  `)
  const { name, shortBio } = siteMetadata
  return (
    <ProfileStyle>
      <img src="/assets/profile.jpg" alt={name} />
      <div>
        <h4>{name}</h4>
        <p>{shortBio}</p>
      </div>
    </ProfileStyle>
  )
}

export default Profile
